import React, { Component } from 'react';
import './App.css';
import Background from './Background/Background';
import Button from './Button/Button';
let body = document.getElementById("gradient");

class App extends Component {

  changeColor = () => {
    let randomArray = this.randomrgbArray();
    let joinedFirst = randomArray.slice(0,3).join();
    let joinedSecond = randomArray.slice(3,).join();
    body.style.background = "linear-gradient(to right, " + "rgb(" + joinedFirst + ")," + "rgb(" + joinedSecond + "))";
  }

  randomrgbArray = () => {
    let a = [];
    for (var i=0; i < 6; i++) {
        a[i] = Math.floor((Math.random() * 255) + 1);
    }
    return a;
  }

  render() {
    return (
      <div className="App">
        <Background></Background>
        <Button click={this.changeColor}></Button>
      </div>
    );
  }
}

export default App;
